




















































































































































































































































































































































































@import '../styles/variables/colors.styl'
@import "../styles/variables/dropdown.styl"
@import '../styles/mixins/text.styl'
@import '../styles/mixins/utils.styl'

$rowChangeHighlightColor = $globalColorYellowOrange
$cellVerticalPadding = 10px

//      ,    `--scrollbar-width`
// -     .
$scrollbarWidth__fallback = 15px


.baseTable-wrapper
    width 100%
    overflow-x auto
    overflow-y hidden

.baseTable
    min-width 100%
    height 100%
    // `inline-flex`,   `flex`,    `flex`  `.baseTable`
    // (          )
    //     ,  ,   `inline-flex` -  
    //   -     ,
    //      .    .
    display inline-flex
    flex-direction column
    position relative

.baseTable__header
    flex 0 0 auto
    display flex
    // `baseTable__row`      ,
    //     `column`
    flex-direction column

.baseTable__body
    flex 1 1 auto
    display flex
    flex-direction column
    //       ,
    //     
    overflow-y scroll

.baseTable__row
    flex 0 0 auto
    display flex
    text-decoration none
    border-bottom 1px solid $globalColorMystic

    //       ,
    // ,        ,
    //   ,  ,   
    &.--header
        border-right var(--scrollbar-width, $scrollbarWidth__fallback) solid transparent

    &:hover:not(.--header):not(.--checked)
         background-color $globalColorCatskillWhite

    //   
    &.--checked
        background $globalColorBlackSqueeze


.baseTable__cell
    commonTextStyles()
    position relative
    padding $cellVerticalPadding 24px $cellVerticalPadding 0
    vertical-align middle
    text-align left
    color $globalColorNileBlue
    overflow-x hidden
    white-space normal
    text-overflow ellipsis

    &.--header
        display flex
        align-items center
        white-space nowrap

        .baseTable__cell__headerText
            font-weight bold

    &.--sortable
        cursor pointer

    &.--actions
        // .   `.baseTable__cell__actions`
        width $dropdownTriggerWidth

        //       `overflow-x hidden`,
        //        
        overflow visible

.baseTable__cell__sortingIndicator
    $sortingIndicatorWidth = 8px

    flex 0 0 auto
    width $sortingIndicatorWidth
    height ($sortingIndicatorWidth * 2) + 2px
    margin 0 7px
    display flex
    flex-direction column
    justify-content space-between

    &::before
    &::after
        content ''
        border ($sortingIndicatorWidth / 2) solid transparent

    &::before
        border-bottom-color $globalColorMischka
    &::after
        border-top-color $globalColorMischka

    &.--ascending::before
        border-bottom-color $globalColorNileBlue
    &.--descending::after
        border-top-color $globalColorNileBlue


.baseTable__cell__actions
    //         ,
    //     (  ) +  ,
    //      .
    // `!important`   `position relative`
    //      
    position absolute !important
    top 'calc(50% - %s)' % ($dropdownTriggerHeight / 2)


//  
.baseTable__checkbox
    width 14px
    height @width
    color #ffffff
    fill $globalColorShakespeare

    .baseTable__cell &
        margin-left 10px

